html, body {
  background-color: #222;
  height: 100%;
  width: 100%; }

.App {
  background-color: #222;
  border: solid 1px green;
  top: 30px;
  padding: 30px;
  width: 95%;
  margin: 0 auto;
  position: relative;
  text-align: center; }
  .App .headerSpan {
    display: inline-block;
    color: red;
    width: 100px;
    text-align: left;
    padding: 5px; }
  .App h3 {
    color: white;
    font-family: "Raleway", sans-serif; }
  .App .container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-direction: column;
        flex-direction: column;
    width: 50%;
    margin-top: 50px; }
    .App .container button {
      background-color: #1A3F16;
