      border-color: #36842F; }

.InfoModal, .modalHead, .modalBod, .modalFoot {
  background-color: #222;
  border: solid 2px green;
  color: white;
  font-size: 1.1em; }

.modalHead {
  text-align: center;
  font-family: "Raleway", sans-serif; }
  .modalHead span {
    color: white; }

.modalBod {
  border-bottom: none;
  border-top: none;
  font-family: "Roboto Slab", serif;
  font-weight: 100;
  font-size: 1em; }
  .modalBod span {
    color: green;
